import { Box, styled, Typography } from '@material-ui/core';
import { CurrencyAmount, Fraction } from '@uniswap/sdk';
import { CurrencyLogo, CustomTooltip } from 'components';
import moment from 'moment';
import React, { ReactNode } from 'react';
import { useDerivedTwapInfo, useTwapState } from 'state/twap/hooks';
import { formatTokenAmount } from 'utils';
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';
import { useTranslation } from 'react-i18next';
import { Field } from 'state/twap/actions';
const calculateInterval = (value?: number) => {
  if (!value) {
    return '0';
  }
  const time = moment.duration(value);
  const days = time.days();
  const hours = time.hours();
  const minutes = time.minutes();

  let str = '';

  if (days) {
    str += `${days} Days `;
  }
  if (hours) {
    str += `${hours} Hours `;
  }
  if (minutes) {
    str += `${minutes} Minutes`;
  }
  return str;
};

interface Props {
  outputAmount: Fraction;
}

function TxDetails({ outputAmount }: Props) {
  const {
    duration,
    totalChunks,
    interval,
    deadline,
    allowLimitPrice,
  } = useTwapState();
  const { t } = useTranslation();
  const { currencies } = useDerivedTwapInfo();

  const outputCurrency = currencies[Field.OUTPUT];

  return (
    <StyledDetails className='swapBox bg-secondary2'>
      <Row
        title='Expiration date:'
        value={moment(deadline).format('DD/MM/YYYY HH:mm')}
        tooltip={t('confirmationExpiration')}
      />
      <Row
        title='Minimum Received Per Trade:'
        element={
          allowLimitPrice && (
            <StyledCurrency>
              <Typography>
                <span style={{ fontSize: 12 }}>{outputCurrency?.symbol}</span>
              </Typography>
              <Typography>
                <span>{outputAmount.toSignificant(6)}</span>
              </Typography>
              <CurrencyLogo currency={outputCurrency} size='20px' />
            </StyledCurrency>
          )
        }
        value={!allowLimitPrice ? 'None' : undefined}
        tooltip={
          allowLimitPrice
            ? t('confirmationMinimumReceivedWithLimit')
            : t('confirmationMinimumReceivedNoLimit')
        }
      />

      <Row
        title='Total Trades:'
        value={totalChunks || 0}
        tooltip={t('confirmationTotalTrades')}
      />
      <Row
        title='Trade Interval:'
        value={calculateInterval(interval.milliseconds)}
        tooltip={t('confirmationTradeInterval')}
      />
    </StyledDetails>
  );
}

export default TxDetails;

const StyledDetails = styled(Box)({
  display: 'flex',
  flexDirection: 'column',
  gap: 10,
  '& p': {
    display: 'flex',
    alignItems: 'center',
    gap: 7,
    fontSize: 13,
    '& span': {
      fontWeight: 600,
    },
  },
});

const StyledCurrency = styled(Box)({
  display: 'flex',
  alignItems: 'center',
  gap: 5,
});

type RowProps = {
  title: string;
  value?: string | number;
  tooltip?: string;
  element?: ReactNode;
};

const Row = ({ title, value, tooltip, element }: RowProps) => {
  return (
    <StyledRow>
      {tooltip ? (
        <CustomTooltip title={tooltip} placement='right'>
          <Typography>
            {title} <HelpOutlineIcon style={{ width: 15 }} />
          </Typography>
        </CustomTooltip>
      ) : (
        <Typography>{title}</Typography>
      )}
      {value && (
        <Typography>
          <span>{value}</span>
        </Typography>
      )}
      {element}
    </StyledRow>
  );
};

const StyledRow = styled(Box)({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
});
